

















import Vue from "vue";

export default Vue.extend({
    data: function () {
        return {
            lastLivePageInfo: null as Object | null
        };
    },

    mounted: function () {
        let lastLivePageString = localStorage.getItem("LastLivePage");
        if (lastLivePageString != null)
            this.lastLivePageInfo = JSON.parse(lastLivePageString);
    }
});
